import * as THREE from 'three';

import { TextureLoader } from 'three';
import {SVGLoader} from 'three/examples/jsm/loaders/SVGLoader';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'; // Import OrbitControls
import {slider_values} from './main';

export class Layers3D {
  constructor() {
    // Initialize the scene, camera, and renderer
    this.scene = new THREE.Scene();
    this.camera = new THREE.PerspectiveCamera(50, 4.0/6.0, 0.01, 1000);
    this.camera.position.set(-799,-100,639);
    this.renderer = new THREE.WebGLRenderer();
    this.renderer.setSize(400, 600);

    this.canvasVG = document.getElementById("layers3D-container");
    this.canvasVG.appendChild(this.renderer.domElement);

    // Create an instance of OrbitControls
    this.controls = new OrbitControls(this.camera, this.renderer.domElement);
    this.controls.enableDamping = true; // Add damping for smoother controls
    this.controls.dampingFactor = 0.05;

    // Axes helper
    const axesHelper = new THREE.AxesHelper(500);

    this.svgGroups = [null, null, null, null];
    this.interpolatedSVG = [null, null, null];
    this.svgMarkups = ["", "", "", ""];

    this.renderer.setClearColor(0xFFFFFF);

    this.frame_markers = slider_values;

    //Time one
    this.interpolatedSVG = [];
    this.time = 0.0;

    this.top = 140
    this.bottom = -70;

    document.addEventListener('keydown', (event) => {

    });
    // Add a keydown event listener to move the camera
    document.addEventListener('keydown', (event) => {
      const cameraSpeed = 5; // Adjust the speed as needed
      const step = 20; // Adjust the step size as needed
      if (event.key === 'p' || event.key === 'P') {
        const svgMarkups = [
          '<svg id="svg" version="1.1" width="285" height="244" xmlns="http://www.w3.org/2000/svg"><path d="M67.500 51.957 C 58.104 53.995,39.393 62.812,38.274 65.728 C 37.287 68.301,43.818 79.957,49.704 86.128 C 56.002 92.731,82.110 109.870,102.887 121.041 C 109.824 124.771,117.456 129.051,119.846 130.552 C 125.911 134.362,132.318 142.805,137.817 154.235 C 140.393 159.589,143.062 163.976,143.749 163.985 C 144.436 163.993,148.036 162.477,151.749 160.615 C 162.318 155.315,168.603 153.588,177.210 153.618 C 184.172 153.643,185.628 154.015,192.210 157.456 C 215.912 169.845,217.204 170.287,233.505 171.601 L 241.509 172.247 242.755 168.802 C 243.440 166.908,244.000 164.286,244.000 162.975 C 244.000 159.425,235.749 143.303,230.651 136.891 C 225.676 130.632,223.004 128.782,204.887 119.041 C 197.949 115.311,184.403 107.249,174.784 101.124 C 156.572 89.530,148.179 82.191,144.193 74.379 L 142.136 70.346 131.611 69.197 C 119.960 67.925,112.411 65.634,104.652 61.016 C 92.592 53.837,87.654 51.981,79.411 51.526 C 75.060 51.286,69.700 51.480,67.500 51.957 M91.500 57.027 C 94.800 58.634,99.819 61.329,102.652 63.016 C 110.411 67.634,117.960 69.925,129.611 71.197 L 140.136 72.346 142.193 76.379 C 146.179 84.191,154.572 91.530,172.784 103.124 C 182.403 109.249,195.949 117.311,202.887 121.041 C 209.824 124.771,217.456 129.051,219.846 130.552 C 225.756 134.264,232.210 142.666,237.635 153.709 C 241.962 162.518,242.102 163.076,240.853 166.531 L 239.546 170.144 233.523 169.508 C 218.375 167.909,217.347 167.541,194.063 155.383 C 186.807 151.594,186.383 151.502,176.563 151.585 C 168.163 151.656,165.281 152.126,159.121 154.428 C 155.063 155.945,150.338 158.042,148.621 159.089 C 146.905 160.136,145.207 160.994,144.849 160.996 C 144.491 160.998,142.154 156.837,139.655 151.750 C 134.212 140.669,127.763 132.268,121.846 128.552 C 119.456 127.051,111.824 122.771,104.887 119.041 C 84.217 107.928,57.999 90.728,51.763 84.191 C 46.852 79.042,41.000 69.750,41.000 67.100 C 41.000 64.944,60.783 55.831,69.500 53.972 C 76.047 52.576,84.876 53.802,91.500 57.027 " stroke="none" fill="black" fill-rule="evenodd"/></svg>',
          '<svg id="svg" version="1.1" width="285" height="244" xmlns="http://www.w3.org/2000/svg"><path d="M67.500 51.957 C 58.104 53.995,39.393 62.812,38.274 65.728 C 37.287 68.301,43.818 79.957,49.704 86.128 C 56.002 92.731,82.110 109.870,102.887 121.041 C 109.824 124.771,117.456 129.051,119.846 130.552 C 125.911 134.362,132.318 142.805,137.817 154.235 C 140.393 159.589,143.062 163.976,143.749 163.985 C 144.436 163.993,148.036 162.477,151.749 160.615 C 162.318 155.315,168.603 153.588,177.210 153.618 C 184.172 153.643,185.628 154.015,192.210 157.456 C 215.912 169.845,217.204 170.287,233.505 171.601 L 241.509 172.247 242.755 168.802 C 243.440 166.908,244.000 164.286,244.000 162.975 C 244.000 159.425,235.749 143.303,230.651 136.891 C 225.676 130.632,223.004 128.782,204.887 119.041 C 197.949 115.311,184.403 107.249,174.784 101.124 C 156.572 89.530,148.179 82.191,144.193 74.379 L 142.136 70.346 131.611 69.197 C 119.960 67.925,112.411 65.634,104.652 61.016 C 92.592 53.837,87.654 51.981,79.411 51.526 C 75.060 51.286,69.700 51.480,67.500 51.957 M91.500 57.027 C 94.800 58.634,99.819 61.329,102.652 63.016 C 110.411 67.634,117.960 69.925,129.611 71.197 L 140.136 72.346 142.193 76.379 C 146.179 84.191,154.572 91.530,172.784 103.124 C 182.403 109.249,195.949 117.311,202.887 121.041 C 209.824 124.771,217.456 129.051,219.846 130.552 C 225.756 134.264,232.210 142.666,237.635 153.709 C 241.962 162.518,242.102 163.076,240.853 166.531 L 239.546 170.144 233.523 169.508 C 218.375 167.909,217.347 167.541,194.063 155.383 C 186.807 151.594,186.383 151.502,176.563 151.585 C 168.163 151.656,165.281 152.126,159.121 154.428 C 155.063 155.945,150.338 158.042,148.621 159.089 C 146.905 160.136,145.207 160.994,144.849 160.996 C 144.491 160.998,142.154 156.837,139.655 151.750 C 134.212 140.669,127.763 132.268,121.846 128.552 C 119.456 127.051,111.824 122.771,104.887 119.041 C 84.217 107.928,57.999 90.728,51.763 84.191 C 46.852 79.042,41.000 69.750,41.000 67.100 C 41.000 64.944,60.783 55.831,69.500 53.972 C 76.047 52.576,84.876 53.802,91.500 57.027 " stroke="none" fill="black" fill-rule="evenodd"/></svg>'
          ];
        this.addSVGs(svgMarkups);
      }else if (event.key === 'ArrowUp') {
        this.camera.position.z += step;
      } else if (event.key === 'ArrowDown') {
        this.camera.position.z -= step;
      } else if (event.key === 'ArrowLeft') {
        this.camera.position.x -= step;
      } else if (event.key === 'ArrowRight') {
        this.camera.position.x += step;
      } else if (event.key === 'U'){
        this.camera.position.y += step;
      } else if (event.key === 'D'){
        this.camera.position.y -= step;
      } else if (event.key === 'i' || event.key === 'I') {
        this.interpolateSVGs();
      }

      this.camera.updateProjectionMatrix();
    });

    this.svgMarkups = [];
    this.animate();

  }

  // Function to add an SVG to the scene
  addSVG(index, svgMarkup) {
    console.log("what about here");
    if (index>=5) {
      console.log("going for interpolated layers");
      this.addSVG_interpolated(index-5,svgMarkup);
      return;
    }
    console.log("index", index);
    index-=1;
    // if (index<=1) this.svgMarkups.push(svgMarkup);
    // Create a group for the new SVG content
    const svgGroup = new THREE.Group();
    svgGroup.scale.y *= -1;
    const material = new THREE.MeshNormalMaterial();

    // Parse the SVG content
    const loader = new SVGLoader();
    const svgData = loader.parse(svgMarkup);

    svgData.paths.forEach((path, i) => {
      const shapes = path.toShapes(true);

      shapes.forEach((shape, j) => {
        const geometry = new THREE.ExtrudeGeometry(shape, {
          depth: 10,
          bevelEnabled: false,
        });

        const mesh = new THREE.Mesh(geometry, material);

        svgGroup.add(mesh);
      });
    });

    // Adjust the position of the new SVG group
    const box = new THREE.Box3().setFromObject(svgGroup);
    const size = new THREE.Vector3();
    box.getSize(size);

    const xOffset = -size.x / 2 ;
    const yOffset = -size.y / 2 ; // Vary the Y position based on index

    svgGroup.children.forEach((item) => {
      item.position.x = xOffset;
      item.position.y = yOffset;
    });

    // Apply the rotation
    svgGroup.rotation.x = Math.PI / 2; // Rotate by 90 degrees
    svgGroup.position.set(50, -(index-2)*70, -20);
    //svgGroup.position.set(50, this.findPositionY(index), -20);

    // Check if there's an existing SVG at the given index
    if (this.svgGroups[index]) {
      console.log("Already exists");
      // Remove the existing children of the SVG group
      //this.svgGroups[index].clear();
      this.svgGroups[index].children.forEach(child => {
        this.svgGroups[index].remove(child);
      });


      // Add the new meshes to the existing SVG group
      svgGroup.children.forEach((mesh) => {
        this.svgGroups[index].add(mesh);
      });
    } else {
      // Add the new SVG group to the scene and update the array
      this.scene.add(svgGroup);
      this.svgGroups[index] = svgGroup;
    }
  }

  addSVG_interpolated(index, svgMarkup) {


    const svgGroup = new THREE.Group();
    svgGroup.scale.y *= -1;
    const material = new THREE.MeshNormalMaterial({ color: 0x808080 });

    // Parse the SVG content
    const loader = new SVGLoader();
    const svgData = loader.parse(svgMarkup);

    svgData.paths.forEach((path, i) => {
      const shapes = path.toShapes(true);

      shapes.forEach((shape, j) => {
        const geometry = new THREE.ExtrudeGeometry(shape, {
          depth: 10,
          bevelEnabled: false,
        });

        const mesh = new THREE.Mesh(geometry, material);

        svgGroup.add(mesh);
      });
    });

    // Adjust the position of the new SVG group
    const box = new THREE.Box3().setFromObject(svgGroup);
    const size = new THREE.Vector3();
    box.getSize(size);

    const xOffset = -size.x / 2 ;
    const yOffset = -size.y / 2 ; // Vary the Y position based on index

    svgGroup.children.forEach((item) => {
      item.position.x = xOffset;
      item.position.y = yOffset;
    });

    // Apply the rotation
    svgGroup.rotation.x = Math.PI / 2; // Rotate by 90 degrees
    //svgGroup.position.set(50, -(index-2)*70, -20);
    svgGroup.position.set(50, this.findPosY(index), -20);

    // Check if there's an existing SVG at the given index
    if (this.interpolatedSVG[index]) {
      // Remove the existing children of the SVG group
      this.interpolatedSVG[0].children.forEach(child => {
        this.interpolatedSVG[0].remove(child);
      });
      // Add the new meshes to the existing SVG group
      svgGroup.children.forEach((mesh) => {
        this.interpolatedSVG[0].add(mesh);
      });
    } else {
      // Add the new SVG group to the scene and update the array
      this.scene.add(svgGroup);
      this.interpolatedSVG[index] = svgGroup;
    }
  }


  addSVGs() {
    this.svgMarkups.forEach((svgMarkup, index) => {
      if (svgMarkup.length !== 0) {
        console.log("Adding ", index + 1);
        this.addSVG(index + 1, svgMarkup);
      }
    });
  }


  updateSVGArray(index,svgmarkup){
    console.log("updating array");
    this.svgMarkups[index-1] = svgmarkup;
  }

  findPositionY(index){


    var a = this.top;
    var b = this.bottom;
    var t = this.frame_markers[index];
    var lerpedY = (1 - t) * a + t * b;

    return lerpedY;

  }

  findPosY(index){
    var a = this.top;
    var b = this.bottom;
    var t = (this.frame_markers[index] + this.frame_markers[index+1])/2.0;
    var lerpedY = (1 - t) * a + t * b;
    return lerpedY;
  }

  changeMarkers(array){
    this.frame_markers = array.slice();
    for (var i=0; i<4;i++) {
      if (this.svgGroups[i]) this.svgGroups[i].position.set(50, this.findPositionY(i), -20);
    }
  }

  changeTime(val){
    this.time = val;
    if (this.interpolatedSVG[0])  this.interpolatedSVG[0].position.set(50, this.findPosY(), -20);
  }

  removeAllSVGs() {
    // Loop through the stored SVG groups and remove them from the scene
    this.svgGroups.forEach((svgGroup) => {
      this.scene.remove(svgGroup);
    });

    // Clear the array of stored SVG groups
    this.svgGroups = [];
  }

  animate() {
    this.renderer.render(this.scene, this.camera);
    this.controls.update();
    requestAnimationFrame(this.animate.bind(this));
  }

}



